/*--------------------------------------------------------------
## Helpers
--------------------------------------------------------------*/
.centered {
  text-align: center;
}

.sr-only {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  word-wrap: normal !important;
  /* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.sr-only:focus {
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  display: block;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/*--------------------------------------------------------------
## Print
--------------------------------------------------------------*/
@media print {
  body {
    background: #fff;
    color: #000;
    margin: 10px 0;
    padding: 0;
    text-align: left;
  }
  a:link, a:visited {
    background: #fff;
    color: #000;
    text-decoration: underline;
  }
  a:not([href="javascript:;"])::after {
    content: " [" attr(href) "] ";
  }
  h1, h2, h3, h4, h5, h6, span {
    background: none;
    color: #000 !important;
  }
  .temp,
  .site-header,
  .site-footer,
  .banner,
  .entry-footer {
    display: none;
  }
  /*--------------------------------------------------------------
	## Content Split
	--------------------------------------------------------------*/
  .content-split {
    flex-direction: column;
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 0;
  }
  .content-split-media-wide {
    max-width: 100%;
  }
}

