/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
  color: #25282a;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  line-height: 1.6;
}

blockquote {
  margin: 0 25px;
}

pre {
  max-width: 100%;
  padding: 1.6em;
  overflow: auto;
  background: #bebebe;
}

[class*='font-'] {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #000;
  font-size: 30px;
  line-height: 1;
}

p::selection,
h1::selection,
h2::selection,
h3::selection {
  background: #4a4a4a;
  color: #ef4035;
}

/*--------------------------------------------------------------
## Base Layout
--------------------------------------------------------------*/
html, body {
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -ms-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  height: 100%;
}

html {
  box-sizing: border-box;
}

/*--------------------------------------------------------------
## Layout
--------------------------------------------------------------*/
.wrapper {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 15px;
}

/*--------------------------------------------------------------
## Animations
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Parts
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Buttons
--------------------------------------------------------------*/
.btn {
  display: inline-block;
  padding: 15px 36px;
  transition: 350ms ease;
  font-size: 13px;
  text-align: center;
  text-transform: uppercase;
}

.btn:active, .btn:focus {
  outline: none;
}

@media (max-width: 1024.98px) {
  .btn {
    padding: 7px 15px;
    font-size: 11px;
  }
}

/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Forms
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Wrapper and Ratios
--------------------------------------------------------------*/
img {
  max-width: 100%;
  display: block;
  height: initial;
}

